.my-carousel { 
    background: #fff;
}
.my-carousel a {
  display: inline-block;
  width: 100%;
  margin: 0; padding: 0;
}
.my-carousel a img {
  width: 100%;
  vertical-align: top;
  height: 3.6rem;
  -o-object-fit: cover;
     object-fit: cover;
}
.my-carousel .v-item {
  height: 0.72rem;
  line-height: 0.72rem;
  padding-left: 0.2rem;
}
.type-block {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 0.1rem;
  display: inline-block;
  position: relative;
}

.type-row {
  position: relative;
  overflow: hidden;
  overflow-y: auto;
  overflow-x: scroll;
  margin-left: -0.2rem;
  padding: 0 0.1rem;
}

.type-title {
  margin-left: 0.1rem;
}

.type-row img {
  width: 100%;
  border-radius: 0.1rem;
  -o-object-fit: cover;
     object-fit: cover;
  height: 1.6rem;
}

.type-row p {
  color: #888888;
}

.type-all {
  margin: 0 0 0 0.2rem;
}
.cut-line {
  background-color: #888;
  width: 100%;
  height: 0.01rem;
}
::-webkit-scrollbar {
  width: 0;
}


